@import "https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css";
@import "https://fonts.googleapis.com/css?family=Inter:500,700,800,400|Bevan:400|Work+Sans:600|IBM+Plex+Mono:400,600|Poppins:500";
body {
  background-color: #f9f9f9;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
}

.screen a {
  color: inherit;
  text-decoration: none;
  display: contents;
}

.container-center-horizontal {
  pointer-events: none;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  display: flex;
}

.container-center-horizontal > * {
  pointer-events: auto;
  flex-shrink: 0;
}

.align-self-flex-start {
  align-self: flex-start;
}

.align-self-flex-end {
  align-self: flex-end;
}

.align-self-flex-center {
  align-self: center;
}

.valign-text-middle {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

input:focus {
  outline: none;
}

* {
  box-sizing: border-box;
}

input {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

@media only screen and (width <= 1200px) {
  .container-center-horizontal {
    max-width: 100%;
    padding: 0 20px;
  }

  input {
    width: 100%;
    max-width: 400px;
  }
}

@media only screen and (width <= 768px) {
  body {
    font-size: 14px;
  }

  .container-center-horizontal {
    text-align: center;
    flex-direction: column;
    align-items: center;
  }

  .align-self-flex-center {
    text-align: center;
  }

  input {
    width: 100%;
    max-width: 300px;
  }
}

@media only screen and (width <= 480px) {
  body {
    padding: 10px;
  }

  .valign-text-middle {
    font-size: 12px;
  }

  .container {
    padding: 10px;
  }

  input {
    font-size: 14px;
  }
}

.loading-screen {
  z-index: 9999;
  background-color: #f8f9fa;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity .5s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loading-container {
  text-align: center;
  background-color: #fff;
  border-radius: 15px;
  width: 80%;
  max-width: 500px;
  padding: 30px;
  animation: .8s ease-out fadeInUp;
  box-shadow: 0 10px 30px #0000001a;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.loading-container h2 {
  color: #333;
  margin-bottom: 25px;
  font-size: 26px;
  font-weight: 600;
  display: inline-block;
  position: relative;
}

.loading-container h2:after {
  content: "";
  background: linear-gradient(90deg, #4a90e2, #63b3ed);
  border-radius: 2px;
  width: 50%;
  height: 3px;
  position: absolute;
  bottom: -8px;
  left: 25%;
}

.progress-bar-container {
  background-color: #e9ecef;
  border-radius: 10px;
  width: 100%;
  height: 20px;
  margin-bottom: 15px;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 2px 5px #0000001a;
}

.progress-bar {
  border-radius: 10px;
  height: 100%;
  transition: width .3s ease-in-out;
  position: relative;
  overflow: hidden;
}

.progress-text {
  color: #4a90e2;
  letter-spacing: 1px;
  margin-top: 10px;
  font-size: 18px;
  font-weight: bold;
}

