:root {
  --primary-color: #1e4549;
  --primary-light: #3a6d71;
  --primary-dark: #0a2a2e;
  --secondary-color: #f0e6d2;
  --secondary-light: #fbeed5;
  --accent-color: #004494;
  --text-dark: #333;
  --text-light: #fff;
  --background-light: #fff;
  --background-dark: #f5f5f5;
  --error-color: #e30613;
  --success-color: #28a745;
  --spacing-xs: 8px;
  --spacing-sm: 16px;
  --spacing-md: 24px;
  --spacing-lg: 32px;
  --spacing-xl: 48px;
  --border-radius-sm: 8px;
  --border-radius-md: 20px;
  --border-radius-lg: 40px;
  --box-shadow: 0px 4px 11px #1e454945;
  --box-shadow-hover: 0px 6px 15px #1e454959;
  --transition-fast: all .2s ease-in-out;
  --transition-medium: all .3s ease-in-out;
}

.acquisition-container {
  background-color: var(--background-light);
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  font-family: Inter, sans-serif;
  display: flex;
  overflow-x: hidden;
}

.acquisition-header {
  background-color: var(--secondary-color);
  padding: var(--spacing-md) var(--spacing-xl);
  box-shadow: var(--box-shadow);
  z-index: 10;
  border-radius: 0 0 40px 40px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
}

.acquisition-content {
  padding: var(--spacing-xl);
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  animation: .5s ease-in-out fadeIn;
  display: flex;
}

.acquisition-title {
  color: var(--primary-color);
  margin-bottom: var(--spacing-lg);
  text-align: center;
  font-size: 32px;
  font-weight: 700;
  position: relative;
}

.acquisition-title:after {
  content: "";
  background-color: var(--primary-light);
  border-radius: 3px;
  width: 60px;
  height: 3px;
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-5px);
  }

  20%, 40%, 60%, 80% {
    transform: translateX(5px);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.acquisition-form {
  gap: var(--spacing-lg);
  background-color: var(--background-light);
  width: 100%;
  padding: var(--spacing-lg);
  border-radius: var(--border-radius-md);
  box-shadow: var(--box-shadow);
  transition: var(--transition-medium);
  flex-direction: column;
  display: flex;
}

.acquisition-form:hover {
  box-shadow: var(--box-shadow-hover);
}

.form-group {
  gap: var(--spacing-xs);
  flex-direction: column;
  display: flex;
}

.form-group label {
  color: var(--primary-color);
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

.form-input, .form-select {
  border: 2px solid var(--secondary-color);
  border-radius: var(--border-radius-sm);
  padding: var(--spacing-md);
  transition: var(--transition-fast);
  font-size: 16px;
}

.form-input:focus, .form-select:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px #1e454933;
}

.form-input::placeholder, .form-select::placeholder {
  color: #b0b0b0;
}

.form-select {
  appearance: none;
  cursor: pointer;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='%231E4549' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-position: right 1rem center;
  background-repeat: no-repeat;
  background-size: 1em;
}

.form-select option {
  padding: var(--spacing-xs);
  font-size: 16px;
}

.form-select option:first-child {
  color: #b0b0b0;
}

.form-select:hover {
  border-color: var(--primary-light);
}

.btn-primary, .btn-secondary {
  padding: var(--spacing-sm) var(--spacing-lg);
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  transition: var(--transition-medium);
  border: none;
