:root {
  --primary-color: #1e4549;
  --primary-light: #3a6d71;
  --primary-dark: #0a2a2e;
  --secondary-color: var(--rum-swizzle);
  --secondary-light: #fbeed5;
  --accent-color: #004494;
  --text-dark: #333;
  --text-light: #fff;
  --background-light: #fff;
  --background-dark: #f5f5f5;
  --spacing-xs: 8px;
  --spacing-sm: 16px;
  --spacing-md: 24px;
  --spacing-lg: 32px;
  --spacing-xl: 48px;
  --border-radius-sm: 8px;
  --border-radius-md: 20px;
  --border-radius-lg: 40px;
  --box-shadow: 0px 4px 11px #1e454945;
  --box-shadow-hover: 0px 6px 15px #1e454959;
  --transition-fast: all .2s ease-in-out;
  --transition-medium: all .3s ease-in-out;
}

.measuring-form-container {
  background-color: var(--background-light);
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  font-family: Inter, sans-serif;
  display: flex;
  overflow-x: hidden;
}

.measuring-form-header {
  background-color: var(--secondary-color);
  padding: var(--spacing-md) var(--spacing-xl);
  box-shadow: var(--box-shadow);
  z-index: 10;
  border-radius: 0 0 40px 40px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
}

.measuring-form-content {
  padding: var(--spacing-xl);
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  animation: .5s ease-in-out fadeIn;
  display: flex;
}

.measuring-form-title {
  color: var(--primary-color);
  margin-bottom: var(--spacing-md);
  text-align: center;
  font-size: 32px;
  font-weight: 700;
  position: relative;
}

.measuring-form-title:after {
  content: "";
  background-color: var(--primary-light);
  border-radius: 3px;
  width: 60px;
  height: 3px;
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
}

.measuring-form-description {
  color: var(--text-dark);
  margin-bottom: var(--spacing-xl);
  text-align: center;
  max-width: 600px;
  font-size: 18px;
}

.criteria-grid {
  gap: var(--spacing-lg);
  width: 100%;
  margin-bottom: var(--spacing-xl);
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  display: grid;
}

.criterion-card {
  background-color: var(--background-light);
  border-radius: var(--border-radius-md);
  box-shadow: var(--box-shadow);
  padding: var(--spacing-lg);
  transition: var(--transition-medium);
  text-align: center;
  align-items: center;
  gap: var(--spacing-md);
  border: 2px solid #0000;
  flex-direction: column;
  height: 100%;
  text-decoration: none;
  display: flex;
}

.criterion-card:hover {
  box-shadow: var(--box-shadow-hover);
  border-color: var(--primary-light);
  transform: translateY(-5px);
}

.criterion-icon {
  margin-bottom: var(--spacing-sm);
  background-color: var(--secondary-light);
  width: 80px;
  height: 80px;
  transition: var(--transition-medium);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 48px;
  display: flex;
}

.criterion-card:hover .criterion-icon {
  background-color: var(--primary-light);
  transform: scale(1.1);
}

.criterion-name {
  color: var(--primary-color);
  transition: var(--transition-fast);
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

.criterion-card:hover .criterion-name {
  color: var(--primary-dark);
}

.status-indicator {
  align-items: center;
  gap: 8px;
  margin-top: 12px;
  display: flex;
}

.status-dot {
  background-color: #e63946;
  border-radius: 50%;
  width: 12px;
  height: 12px;
}

.status-text {
  color: var(--text-dark);
  font-size: 14px;
}

.criterion-card.completed .status-dot {
  background-color: #2ecc71;
}

.criterion-card.incomplete .status-dot {
  background-color: #e63946;
}

.criterion-card.completed {
  border-left: 4px solid #2ecc71;
}

.criterion-card.incomplete {
  border-left: 4px solid #e63946;
}

.criterion-card.completed .status-text {
  color: #2ecc71;
  font-weight: 600;
}

.criterion-card.incomplete .status-text {
  color: #e63946;
  font-weight: 600;
}

.btn-primary {
  background-color: var(--primary-color);
  color: var(--text-light);
  padding: var(--spacing-sm) var(--spacing-lg);
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  transition: var(--transition-medium);
  margin-left: var(--spacing-md);
  border: none;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  display: inline-flex;
}

.btn-primary:hover {
  background-color: var(--primary-dark);
  box-shadow: var(--box-shadow);
  transform: translateY(-3px);
}

.form-actions {
  justify-content: center;
  gap: var(--spacing-md);
  margin-top: var(--spacing-xl);
  display: flex;
}

.measuring-form-footer {
  margin-top: var(--spacing-xl);
  justify-content: center;
  display: flex;
}

.btn-secondary {
  background-color: var(--secondary-color);
  color: var(--primary-color);
  padding: var(--spacing-sm) var(--spacing-lg);
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  transition: var(--transition-medium);
  border: none;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  display: inline-flex;
}

@media (width <= 768px) {
  .measuring-form-header {
    gap: var(--spacing-md);
    padding: var(--spacing-sm);
    border-radius: 0 0 20px 20px;
    flex-direction: column;
    position: relative;
  }

  .measuring-form-title {
    font-size: 24px;
  }

  .measuring-form-description {
    font-size: 16px;
  }

  .criteria-grid {
    grid-template-columns: 1fr;
  }

  .measuring-form-content {
    padding: var(--spacing-md);
  }
}

@media (width <= 480px) {
  .measuring-form-title {
    font-size: 22px;
  }

  .criterion-card {
    padding: var(--spacing-md);
  }

  .criterion-icon {
    width: 60px;
    height: 60px;
    font-size: 36px;
  }
}

