.modern-dashboard {
  background-color: #f8f9fa;
  border-radius: 12px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  box-shadow: 0 4px 15px #00000014;
}

.dashboard-header {
  border-bottom: 1px solid #eaeaea;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  display: flex;
}

.enterprise-title {
  color: #2d3748;
  margin: 0;
  font-size: 28px;
  font-weight: 700;
}

.enterprise-info {
  color: #4a5568;
  margin-top: .5rem;
  font-size: 16px;
}

.btn-primary {
  color: #fff;
  background-color: #3182ce;
  border-radius: 6px;
  padding: .75rem 1.5rem;
  font-weight: 600;
  text-decoration: none;
  transition: background-color .2s;
  display: inline-block;
}

.score-overview {
  background: #fff;
  border-radius: 10px;
  margin-bottom: 2rem;
  padding: 1.5rem;
  box-shadow: 0 2px 10px #0000000d;
}

.total-score-display {
  align-items: center;
  gap: 2rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.score-circle {
  background: conic-gradient(#4caf50 var(--percentage), #e0e0e0 var(--percentage));
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 150px;
  display: flex;
  position: relative;
}

.score-circle:before {
  content: "";
  background: #fff;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  position: absolute;
}

.score-value {
  z-index: 2;
  color: #333;
  font-size: 24px;
  font-weight: bold;
  position: relative;
}

.score-details {
  flex: 1;
}

.score-details h2 {
  color: #2d3748;
  margin-bottom: 1rem;
  font-size: 22px;
  font-weight: 600;
}

.score-bar {
  background-color: #e2e8f0;
  border-radius: 4px;
  height: 8px;
  margin: .8rem 0;
  overflow: hidden;
}

.score-bar-fill {
  background-color: #4caf50;
  border-radius: 4px;
  height: 100%;
  transition: width 1s ease-in-out;
}

.score-cards-container {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
  display: grid;
}

.score-card {
  background: #fff;
  border-radius: 8px;
  padding: 1.2rem;
  transition: transform .2s, box-shadow .2s;
  box-shadow: 0 2px 8px #0000000f;
}

.score-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px #0000001a;
}

.score-card-header {
  color: #2d3748;
  margin-bottom: 1rem;
  font-size: 18px;
  font-weight: 600;
}

.score-card-value {
  color: #3182ce;
  margin-bottom: .5rem;
  font-size: 32px;
  font-weight: bold;
}

.score-card a {
  color: #3182ce;
  margin-top: .5rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .2s;
  display: inline-block;
}

.score-card a:hover {
  color: #2c5282;
  text-decoration: underline;
}

.score-breakdown {
  margin-top: 2rem;
}

.breakdown-title {
  color: #2d3748;
  margin-bottom: 1rem;
  font-size: 20px;
  font-weight: 600;
}

.breakdown-table {
  border-collapse: collapse;
  width: 100%;
}

.breakdown-table th, .breakdown-table td {
  text-align: left;
  border-bottom: 1px solid #edf2f7;
  padding: .75rem 1rem;
}

.breakdown-table th {
  color: #4a5568;
  background-color: #f7fafc;
  font-weight: 600;
}

.breakdown-table td {
  color: #2d3748;
}

.badge {
  border-radius: 4px;
  padding: .25rem .5rem;
  font-size: .875rem;
  font-weight: 500;
  display: inline-block;
}

.badge-success {
  color: #22543d;
  background-color: #c6f6d5;
}

.badge-warning {
  color: #7b341e;
  background-color: #feebc8;
}

.badge-danger {
  color: #822727;
  background-color: #fed7d7;
}

.loading-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error-message {
  text-align: center;
  color: #822727;
  background-color: #fed7d7;
  border-radius: 8px;
  max-width: 500px;
  margin: 2rem auto;
  padding: 1rem;
}

.error-link {
  color: #3182ce;
  margin-top: 1rem;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.error-link:hover {
  text-decoration: underline;
}

@media (width <= 768px) {
  .nav-items-3-67 {
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  .total-score-display {
    flex-direction: column;
    align-items: center;
  }

  .score-cards-container {
    grid-template-columns: 1fr;
  }

  .dashboard-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
}

