  width: 100%;
  min-height: 100vh;
  font-family: var(--font-family-inter);
  background: linear-gradient(135deg, #fdfcfb, #e2d1c3);
  flex-direction: column;
  display: flex;
  overflow-x: hidden;
}

.environmental-header {
  background-color: var(--secondary-color);
  padding: var(--spacing-md) var(--spacing-xl);
  border-radius: 0 0 var(--border-radius-lg) var(--border-radius-lg);
  box-shadow: var(--box-shadow);
  z-index: 10;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
}

.environmental-content {
  padding: var(--spacing-xl);
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  animation: .5s ease-in-out fadeIn;
  display: flex;
}

.environmental-title {
  color: var(--primary-color);
  margin-bottom: var(--spacing-lg);
  text-align: center;
  font-size: 28px;
  font-weight: 700;
  font-family: var(--font-family-inter);
  position: relative;
}

.environmental-title:after {
  content: "";
  background-color: var(--primary-light);
  border-radius: 3px;
  width: 60px;
  height: 3px;
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
}

.question-card {
  background-color: var(--background-light);
  border-radius: var(--border-radius-md);
  padding: var(--spacing-lg);
  box-shadow: var(--box-shadow);
  transition: var(--transition-medium);
  border-left: 4px solid var(--primary-color);
  opacity: 0;
  animation: .5s ease-out forwards fadeInUp;
}

.question-card:first-child {
  animation-delay: .1s;
}

.question-card:nth-child(2) {
  animation-delay: .2s;
}

.question-card:nth-child(3) {
  animation-delay: .3s;
}

.question-card:nth-child(4) {
  animation-delay: .4s;
}

.question-card:nth-child(5) {
  animation-delay: .5s;
}

.question-card:nth-child(6) {
  animation-delay: .6s;
}

.question-actions {
  gap: var(--spacing-md);
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.btn-option {
  padding: var(--spacing-sm) var(--spacing-xl);
  border-radius: var(--border-radius-sm);
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
  cursor: pointer;
  transition: var(--transition-medium);
  min-width: 120px;
  font-size: 16px;
  font-weight: 600;
  font-family: var(--font-family-inter);
  background-color: #0000;
  position: relative;
  overflow: hidden;
}

.btn-option:hover:not(:disabled) {
  background-color: #1e45491a;
}

.btn-option.enabled {
  background-color: var(--primary-color);
  color: var(--text-light);
}

.btn-option.enabled:after {
  content: "✓";
  font-size: 14px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.btn-primary, .btn-secondary {
  padding: var(--spacing-sm) var(--spacing-lg);
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  transition: var(--transition-medium);
  font-size: 16px;
  font-weight: 600;
  font-family: var(--font-family-inter);
  border: none;
  position: relative;
  overflow: hidden;
}

