.modernization-container {
  background-color: var(--background-light);
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  font-family: Inter, sans-serif;
  display: flex;
  overflow-x: hidden;
}

.modernization-header {
  background-color: var(--secondary-color);
  padding: var(--spacing-md) var(--spacing-xl);
  box-shadow: var(--box-shadow);
  z-index: 10;
  border-radius: 0 0 40px 40px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
}

.logo-icam {
  cursor: pointer;
  height: 74px;
  transition: var(--transition-fast);
}

.logo-icam:hover {
  transform: scale(1.05);
}

.nav-container {
  gap: var(--spacing-md);
  flex-direction: column;
  display: flex;
}

.modernization-content {
  padding: var(--spacing-xl);
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  animation: .5s ease-in-out fadeIn;
  display: flex;
}

.modernization-title {
  color: var(--primary-color);
  margin-bottom: var(--spacing-lg);
  text-align: center;
  font-size: 28px;
  font-weight: 700;
  font-family: var(--font-family-inter);
  position: relative;
}

.modernization-title:after {
  content: "";
  background-color: var(--primary-light);
  border-radius: 3px;
  width: 60px;
  height: 3px;
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
}

.user-info {
  align-items: center;
  gap: var(--spacing-sm);
  display: flex;
}

.admin-text {
  color: var(--primary-color);
  font-size: 14px;
  font-weight: 600;
}

.progress-container {
  width: 100%;
  margin: var(--spacing-lg) 0;
  padding: 0 var(--spacing-md);
}

.progress-bar {
  background-color: var(--background-dark);
  border-radius: 6px;
  width: 100%;
  height: 12px;
  overflow: hidden;
  box-shadow: inset 0 1px 3px #0000001a;
}

.progress-fill {
  background-color: var(--primary-color);
  background-image: linear-gradient(45deg, #ffffff26 25%, #0000 25% 50%, #ffffff26 50% 75%, #0000 75%, #0000);
  background-size: 20px 20px;
  border-radius: 6px;
  height: 100%;
  transition: width .5s;
  animation: 1s linear infinite progress-animation;
}

.progress-text {
  margin-top: var(--spacing-xs);
  text-align: center;
  color: var(--primary-color);
  font-size: 16px;
  font-weight: 500;
  font-family: var(--font-family-inter);
}

.questions-container {
  gap: var(--spacing-lg);
  flex-direction: column;
  width: 100%;
  display: flex;
}

.question-card {
  background-color: var(--background-light);
  border-radius: var(--border-radius-md);
  padding: var(--spacing-lg);
  box-shadow: var(--box-shadow);
  transition: var(--transition-medium);
  width: 100%;
}

.question-card:hover {
  box-shadow: var(--box-shadow-hover);
  transform: translateY(-3px);
}

.question-text {
  color: var(--text-dark);
  margin-bottom: var(--spacing-md);
  font-size: 18px;
  font-weight: 600;
  line-height: 1.5;
  font-family: var(--font-family-inter);
}

.question-actions {
  gap: var(--spacing-md);
  justify-content: center;
  display: flex;
}

.btn-option {
  background-color: var(--background-dark);
  color: var(--text-dark);
  border: 2px solid var(--background-dark);
  border-radius: var(--border-radius-sm);
  padding: var(--spacing-sm) var(--spacing-lg);
  cursor: pointer;
  transition: var(--transition-medium);
  justify-content: center;
  align-items: center;
  min-width: 120px;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

.btn-option:hover:not(:disabled) {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: var(--text-light);
  transform: translateY(-3px);
}

.btn-option.enabled {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: var(--text-light);
}

.btn-option:disabled {
  opacity: .6;
  cursor: not-allowed;
}

.form-input {
  border: 2px solid var(--secondary-color);
  border-radius: var(--border-radius-sm);
  padding: var(--spacing-md);
  width: 100%;
  transition: var(--transition-fast);
  font-size: 16px;
}

.form-input:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px #1e454933;
}

.form-input::placeholder {
  color: #b0b0b0;
}

.form-actions {
  gap: var(--spacing-md);
  margin-top: var(--spacing-xl);
  justify-content: center;
  display: flex;
}

.btn-primary, .btn-secondary {
  padding: var(--spacing-sm) var(--spacing-lg);
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  transition: var(--transition-medium);
  border: none;
  justify-content: center;
  align-items: center;
  gap: 8px;
  min-width: 150px;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  display: flex;
}

.btn-primary {
  background-color: var(--primary-color);
  color: var(--text-light);
}

.btn-primary:hover:not(:disabled) {
  background-color: var(--primary-light);
  transform: translateY(-3px);
}

.btn-primary:disabled {
  cursor: not-allowed;
  background-color: #b0b0b0;
}

.btn-secondary {
  background-color: var(--secondary-color);
  color: var(--primary-color);
}

.btn-secondary:hover {
  background-color: var(--secondary-light);
  transform: translateY(-3px);
}

.error-message {
  color: var(--error-color);
  border-radius: var(--border-radius-sm);
  padding: var(--spacing-sm) var(--spacing-md);
