.side-by-side-container {
  display: flex;
  flex-direction: column; /* Arrange items in a column */
  align-items: center; /* Center items horizontally */
  margin-top: 20px; /* Adjust margin as needed */
}

.table-container,
.image-container {
  width: 100%; /* Each container takes up the full width */
  padding: 20px; /* Adjust padding as needed */
  box-sizing: border-box; /* Ensure padding is included in the container size */
}

h2 {
  color: orange;
  font-size: larger;
}

/* Additional styling as needed */
.column h2 {
  color: white;
  font-size: 24px;
}

.column p {
  color: white;
  font-size: 16px;
}
/* InferenceResults.css */

.body {
  padding-top: 350px;
  display: flex;
  justify-content: center;
  color: white;
}

.result-groups {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.result-box {
  width: 350px;
  margin: 10px;
  padding: 15px;
  border: 2px solid #ddd;
  box-sizing: border-box;
  color: var(--tropical-rain-forest);
  border-radius: 10px;
  background-color: var(--rum-swizzle);
}

.scrollable-container {
  max-height: 200px; /* Set a maximum height for the scrollable container */
  overflow-y: auto; /* Enable vertical scrolling when content overflows */
}

.result-item {
  margin-bottom: 100px; /* Espace entre chaque élément de résultat */
}
